
.card-container{
  display:flex;
  flex-direction: column;
  padding:10px;
  justify-content: flex-start;
  gap:10px;
  align-items: center; 
  background-color:white;
  font-size: 14px;
  border-radius:5px;
}

.card-container.left{
  border-bottom: 1px solid #DADADA;
  border-right:1px solid #DADADA;
  height:120px;
  font-size:11px;
  font-weight: 500;
  box-shadow: 0px 7px 20px 0px #28293D14;
}

.card-container.right{
  border-bottom: 1px solid #DADADA;
  border-left:1px solid #DADADA;
  height:120px;
  font-size:11px;
  font-weight: 500;
  box-shadow: 0px 7px 20px 0px #28293D14;
}

.box-border{
  border:1px solid #DADADA;
  min-width:155px;
  height:70px;
  font-size:11px;
  font-weight: 500;
  box-shadow: 0px 7px 20px 0px #28293D14;
}

.horizontal_cards{
  display:flex;
  gap:20px;
  overflow:auto;
  padding:20px;
}

.vertical_cards{
  display:grid;
  grid-template-columns: 50% 50%;
  padding:20px;
}

.horizontal_value{
  font-size:20px;
  font-weight: 500;
  margin: auto;
}

.vertical_value{
  font-size:32px;
  font-weight: 700;
  margin: auto;
}